.pinButton {
  border: none;
  border-radius: 50%;
  height: 5rem;
  width: 5rem;
  font-size: 1.3rem;
  background-color: rgba(255, 255, 255, 0.3);
  box-shadow: 4px 2px 32px 1px rgba(0, 0, 0, 0.3);
  color: #222;
}

.pinButton:hover {
  background-color: rgba(0, 0, 0, 0.3);
  color: #fff;
}
